.AppContainer {
  /* margin: auto; */
  /* width: 800px; */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
  /* border: 1px red solid; */
  box-sizing: border-box;
}

.AppWrapperOne {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  /* border: 1px red solid; */
}

.AppWrapperTwo {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.Header {
  padding-right: 2rem;
}

.ImageLink {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: black;
}

.GithubImage {
  width: 35px;
}

.NameTag {
  margin-left: 0.5rem;
}

.Footer {
  margin-top: 2rem;
  color: #a1a1a1;
}

.FooterLink {
  color: #a1a1a1;
  text-decoration: none;
}

@media only screen and (max-width: 1337px) {
  .AppContainer {
    padding: 1rem;
    position: relative;
    top: unset;
    left: unset;
    transform: none;
  }

  .Header {
    text-align: center;
    font-size: 14px;
    padding-right: unset;
  }

  .Header p {
    margin-top: 6px;
    padding: 0px 30px;
  }

  .NameTag {
    font-size: 13px;
  }

  .GithubImage {
    width: 27px;
  }

  .ImageLink {
    justify-content: center;
  }

  .AppWrapperOne {
    margin-bottom: 20px;
  }

  .AppWrapperOne,
  .AppWrapperTwo {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .AppWrapperOne {
    justify-content: center;
    gap: 30px !important;
  }

  .Footer {
    text-align: center;
    margin: 1rem 0rem 0rem 0rem;
  }
}
